export interface examPreparationStateInterface {
  course_name: string;
  course_code: string;
  description: string;
  school_levels: string[];
  course_id: string;
  exam: ExamItemResponse | null;
  preparation_status: "prepared" | "not prepared";
}

export const FC_reportExamList = (
  allCourses: course[],
  allExams: ExamItemResponse[],
  period: string = ""
): examPreparationStateInterface[] => {
  let result: examPreparationStateInterface[] = [],
    ids: string[] = [];
  for (const key in allCourses) {
    if (Object.prototype.hasOwnProperty.call(allCourses, key)) {
      if (ids.includes(allCourses[key].course_id)) continue;
      ids.push(allCourses[key].course_id);
      let exam = !isEmptyOrSpaces(period)
        ? allExams.find(
            (item) =>
              item.course === allCourses[key].course_id &&
              item.period === period
          )
        : allExams.find((item) => item.course === allCourses[key].course_id);
      if (exam) {
        result.push({
          course_name: allCourses[key].course_name,
          course_code: allCourses[key].course_code,
          description: allCourses[key].description,
          school_levels: allCourses[key].school_levels,
          course_id: allCourses[key].course_id,
          exam: exam,
          preparation_status: "prepared",
        });
      } else {
        result.push({
          course_name: allCourses[key].course_name,
          course_code: allCourses[key].course_code,
          description: allCourses[key].description,
          school_levels: allCourses[key].school_levels,
          course_id: allCourses[key].course_id,
          exam: null,
          preparation_status: "not prepared",
        });
      }
    }
  }
  return result;
};

export const FC_reportExamListFilter = (
  thisList: examPreparationStateInterface[],
  thisStatus: "all" | "prepared" | "not prepared"
): examPreparationStateInterface[] => {
  if (thisStatus === "all") {
    return thisList;
  } else {
    return thisList.filter((item) => item.preparation_status === thisStatus);
  }
};

export const searchExamList = (
  objList: examPreparationStateInterface[],
  text: string
): examPreparationStateInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "course_name" ||
        prop === "course_code" ||
        prop === "preparation_status"
      ) {
        flag =
          product[prop].toString().toLowerCase().indexOf(text.toLowerCase()) >
          -1;
      }
      if (flag) break;
    }
    return flag;
  });
};

type ExamListProps = {
  loading: boolean;
  examsListData: ExamItemResponse[] | null;
  courseListData: course[] | null;
  thisPeriod: SessionPlanInterface[];
  // FOR EXAM PREPARATION PROPS =========================================
  filterPreparation: "prepared" | "not prepared" | "all";
};

const ExamList: React.FC<ExamListProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");
  const [isSorted, setIsSorted] = useState<boolean>(false);
  const [sortProperty, setSortProperty] = useState<string>("");
  const [sortOrder, setSortOrder] = useState<"ASC" | "DESC">("ASC");

  const setSort = (thisProperty: string) => {
    if (sortProperty === thisProperty && sortOrder === "ASC") {
      setSortOrder("DESC");
      setSortProperty(thisProperty);
      setIsSorted(true);
    } else {
      setSortOrder("ASC");
      setSortProperty(thisProperty);
      setIsSorted(true);
    }
  };

  return (
    <Fragment>
      <div className="bg-white my-4">
        <div className="w-full px-2 py-2">
          {props.courseListData && props.examsListData && (
            <input
              onChange={(e) => setSearchInput(e.target.value)}
              type="text"
              value={searchInput}
              placeholder="Search..."
              className="w-full bg-gray-100 py-2 px-3 rounded"
              disabled={
                FC_reportExamListFilter(
                  FC_reportExamList(props.courseListData, props.examsListData),
                  props.filterPreparation
                ).length <= 0
              }
            />
          )}
        </div>
        <table className="w-full shadow" id="report_content">
          <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
            <tr>
              <td className="px-2 py-2">#</td>
              <td
                className="px-2 py-2 cursor-pointer"
                onClick={() => setSort("course_name")}
              >
                <div className="flex items-center gap-1">
                  Course <BiSort />
                </div>
              </td>
              <td className="px-2 py-2">Exam date</td>
              <td className="px-2 py-2">Exam time</td>
              <td className="px-2 py-2">Exam duration</td>
              <td className="px-2 py-2">Exam Period</td>
              <td className="px-2 py-2">Created at</td>
              <td
                className="px-2 py-2 cursor-pointer"
                onClick={() => setSort("preparation_status")}
              >
                <div className="flex items-center gap-1">
                  Exam Status <BiSort />
                </div>
              </td>
            </tr>
          </thead>
          <tbody>
            {props.courseListData === null || props.loading ? (
              <tr>
                <td colSpan={8} className="px-2 py-2">
                  <Loading />
                </td>
              </tr>
            ) : props.courseListData &&
              props.examsListData &&
              FC_reportExamListFilter(
                FC_reportExamList(props.courseListData, props.examsListData),
                props.filterPreparation
              ).length <= 0 ? (
              <tr>
                <td colSpan={8} className="px-2 py-2 text-center">
                  No Course for exam found
                </td>
              </tr>
            ) : (
              props.examsListData &&
              sortThisArray(
                isSorted,
                searchExamList(
                  FC_reportExamListFilter(
                    FC_reportExamList(
                      props.courseListData,
                      props.examsListData
                    ),
                    props.filterPreparation
                  ),
                  searchInput
                ),
                sortProperty,
                sortOrder
              ).map((item, i) => (
                <tr
                  key={i + 1}
                  className={`${
                    item.exam ? "bg-blue-50" : "bg-white"
                  } text-gray-600 hover:bg-blue-100 hover:text-blue-700 border-b border-gray-300 hover:border-blue-500 text-sm`}
                >
                  <td className="px-2 py-2">{i + 1}</td>
                  <td className="px-2 py-2">
                    {capitalizeFirstLetter(item.course_name)} (
                    <span className="font-semibold">{item.course_code}</span>)
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? DATE(item.exam.exam_date) : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? item.exam.start_time : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? item.exam!.duration + " min" : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam
                      ? getPeriodById(props.thisPeriod, item.exam.period)
                        ? getPeriodById(props.thisPeriod, item.exam.period)!
                            .session_plan_name
                        : "-"
                      : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? DATE(item.exam.createdAt) : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {capitalizeFirstLetter(
                      item.exam ? "Prepared" : "Not prepared"
                    )}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </Fragment>
  );
};

export default ExamList;
