import axios from "axios";
import React, { Component } from "react";
import { FaLaptopHouse } from "react-icons/fa";
import { connect } from "react-redux";
import {
  Auth,
  loadCourses,
  loadExamsList,
  PushPopupAlert,
} from "../../../actions";
import { GetAllSessionPlans } from "../../../actions/session";
import { PopupAlertData } from "../../../components/Alert/Popup";
import { SessionPlanInterface, course } from "../../../interfaces";
import {
  centerReportInterface,
  centerReportInterface_NEW,
  roomDistrictInterface,
  roomWithOccupation,
} from "../../../interfaces/center_report";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { ExamItemResponse } from "../../../interfaces/prepare_exam";
import {
  UserByTypeInterfaceAPI,
  UserByTypeKeys,
} from "../../../interfaces/user_by_type";
import { StoreState } from "../../../reducers";
import { SessionPlanReducerInterface } from "../../../reducers/session";
import { isEmptyOrSpaces } from "../../../shared/dataTest";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";
import { examPreparationStateInterface } from "../../Admin/ExamReport/ExamList";
import NoSession from "../../SessionSettings/NoSession";
import PieChart from "../ChartComponents/PieChart";
import ExamDashboardLabels from "../ExamDashboard/ExamDashboardLabels";
import CenterDashboardTitle from "./CenterDashboardTitle";
import RoomChart from "./RoomChart";

export const filterRoom = (
  dataList: centerReportInterface_NEW[],
  thisDistrict: roomDistrictInterface | "all"
): {
  all: centerReportInterface_NEW[];
  full: centerReportInterface_NEW[];
  remain: centerReportInterface_NEW[];
  invigilator: centerReportInterface_NEW[];
} => {
  let result: centerReportInterface_NEW[] = dataList,
    defaultValue: boolean = false;
  if (thisDistrict !== "all") {
    // roomWithOccupation;
    result = result.filter(
      (item) => item.district_id === thisDistrict.district_id
    );
  }
  // RETURN DEFAULT VALUES ====================
  else {
    defaultValue = true;
  }
  return defaultValue
    ? {
        all: dataList.filter((item) => item.room_type !== "Default"),
        full: dataList.filter(
          (item) =>
            item.room_type !== "Default" && item.occupied >= item.capacity
        ),
        remain: dataList.filter(
          (item) =>
            item.room_type !== "Default" && item.occupied < item.capacity
        ),
        invigilator: dataList.filter(
          (item) =>
            item.room_type !== "Default" && !isEmptyOrSpaces(item.invigilator)
        ),
      }
    : {
        all: result,
        full: result.filter((item) => item.occupied >= item.capacity),
        remain: result.filter((item) => item.occupied < item.capacity),
        invigilator: dataList.filter(
          (item) => !isEmptyOrSpaces(item.invigilator)
        ),
      };
};

interface CenterDashboardProps {
  //   REDUX ############################
  auth: Auth;
  sessionPlan: SessionPlanReducerInterface;
  courses: course[] | null;
  examsList: ExamItemResponse[] | null;
  GetAllSessionPlans: Function;
  loadCourses: Function;
  loadExamsList: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface CenterDashboardState {
  loading: boolean;
  loadingExam: boolean;
  selectSession: boolean;
  selectedSession: SessionPlanInterface | null;
  reportMainData: centerReportInterface[] | null;
  formError: {
    element: string;
    msg: string;
  } | null;
  selectedDistrict: roomDistrictInterface | "all";
  chooseDistrict: boolean;
  invigilatorData: UserByTypeInterfaceAPI[] | null;
  selectedExam: examPreparationStateInterface | "all";
  chooseExam: boolean;
}

class _CenterDashboard extends Component<
  CenterDashboardProps,
  CenterDashboardState
> {
  constructor(props: CenterDashboardProps) {
    super(props);
    this.state = {
      loading: true,
      loadingExam: true,
      selectSession: false,
      selectedSession: null,
      reportMainData: null,
      formError: null,
      selectedDistrict: "all",
      chooseDistrict: false,
      invigilatorData: null,
      chooseExam: false,
      selectedExam: "all",
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  setSelectSession = (status: boolean) => {
    this.setState({ selectSession: status });
  };

  setSelectedSession = (thisSession: SessionPlanInterface) => {
    this.setState({ selectedSession: thisSession });
  };

  setThisPeriod = (allSession: SessionPlanInterface[], sessionId: string) => {
    let thisSession: SessionPlanInterface | undefined = allSession.find(
      (item) => item.session_plan_id === sessionId
    );
    if (thisSession) {
      this.setState({ selectedSession: thisSession });
      this.loadReportMainData(thisSession);
    }
  };

  loadReportMainData = async (thisSession: SessionPlanInterface) => {
    try {
      const res = await axios.get<centerReportInterface[]>(
        `${API.APPLICATION}/currentroomoccupation/${thisSession.session_plan_id}`
      );
      console.log("====================================");
      console.log({ reportMainData: res.data });
      console.log("====================================");
      this.setState({ reportMainData: res.data });
    } catch (error) {
      console.error({ reportMainData_error: error });
      // callback(false, errorToText(error));
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  loadInvigilator = async (callback: Function) => {
    callback(true);
    try {
      const res = await axios.get<UserByTypeInterfaceAPI[]>(
        `${API.AUTH}/userbytype/${UserByTypeKeys.INVIGILATOR}`
      );
      console.log("====================================");
      console.log({ invigilatorData: res.data });
      console.log("====================================");
      this.setState({ invigilatorData: res.data });
      callback(false);
    } catch (error) {
      console.error({ invigilatorData_error: error });
      // callback(false, errorToText(error));
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  loadExamData = () => {
    if (this.props.courses === null || this.props.courses.length === 0) {
      this.props.loadCourses((status: boolean) => {
        this.setState({ loadingExam: status });
      });
    } else {
      this.setState({ loadingExam: false });
    }

    if (this.props.examsList === null || this.props.examsList.length === 0) {
      this.props.loadExamsList((status: boolean) => {
        this.setState({ loadingExam: status });
      });
    } else {
      this.setState({ loadingExam: false });
    }
  };

  componentDidMount = () => {
    if (this.state.invigilatorData === null) {
      this.loadInvigilator(this.setLoading);
    }

    if (
      this.props.sessionPlan.allSessionPlans === null ||
      this.props.sessionPlan.allSessionPlans.length === 0
    ) {
      this.props.GetAllSessionPlans(
        (
          status: boolean,
          errorMsg: string,
          sessionData: SessionPlanInterface[]
        ) => {
          this.setLoading(status);
          if (!status) {
            if (!isEmptyOrSpaces(errorMsg)) {
              this.props.PushPopupAlert({
                title: errorMsg,
                theme: PopupAlertTheme.warning,
              });
            }

            if (sessionData && sessionData.length > 0) {
              let activeSession = sessionData.find((item) => item.is_active);
              if (activeSession) {
                this.setSelectedSession(activeSession);
                if (this.state.reportMainData === null) {
                  this.loadReportMainData(activeSession);
                }
              } else {
                this.setSelectedSession(sessionData[sessionData.length - 1]);
                if (this.state.reportMainData === null) {
                  this.loadReportMainData(sessionData[sessionData.length - 1]);
                }
              }
            }
          }
        }
      );
    } else {
      if (this.props.sessionPlan.allSessionPlans.length > 0) {
        let activeSession = this.props.sessionPlan.allSessionPlans.find(
          (item) => item.is_active
        );
        if (activeSession) {
          this.setSelectedSession(activeSession);
          if (this.state.reportMainData === null) {
            this.loadReportMainData(activeSession);
          }
        } else {
          this.setSelectedSession(
            this.props.sessionPlan.allSessionPlans[
              this.props.sessionPlan.allSessionPlans.length - 1
            ]
          );
          if (this.state.reportMainData === null) {
            this.loadReportMainData(
              this.props.sessionPlan.allSessionPlans[
                this.props.sessionPlan.allSessionPlans.length - 1
              ]
            );
          }
        }
        this.setLoading(false);
      }
    }

    if (this.props.courses === null || this.props.courses.length === 0) {
      this.props.loadCourses((status: boolean) => {
        this.setState({ loadingExam: status });
      });
    } else {
      this.setState({ loadingExam: false });
    }

    if (this.props.examsList === null || this.props.examsList.length === 0) {
      this.props.loadExamsList((status: boolean) => {
        this.setState({ loadingExam: status });
      });
    } else {
      this.setState({ loadingExam: false });
    }
  };

  setChooseDistrict = (status: boolean) => {
    this.setState({ chooseDistrict: status });
  };

  setSelectedDistrict = (data: roomDistrictInterface | "all") => {
    this.setState({ selectedDistrict: data });
  };

  setChooseExam = (status: boolean) => {
    this.setState({ chooseExam: status });
  };

  setSelectedExam = (data: examPreparationStateInterface | "all") => {
    this.setState({ selectedExam: data });
  };

  roomDataFilter = (
    allData: centerReportInterface[],
    selectedExam: examPreparationStateInterface | "all"
  ): centerReportInterface[] => {
    if (selectedExam !== "all") {
      return allData.filter((item) =>
        item.course_in_room.filter(
          (element) => element.course_id === selectedExam.course_id
        )
      );
    }
    return allData;
  };

  render() {
    if (
      this.props.sessionPlan.allSessionPlans &&
      this.props.sessionPlan.allSessionPlans.length === 0
    ) {
      return <NoSession />;
    }

    if (
      this.state.selectedSession === null ||
      this.props.sessionPlan.allSessionPlans === null ||
      this.state.reportMainData === null ||
      this.state.invigilatorData === null
    ) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading Data...
        </div>
      );
    }

    return (
      <section>
        <CenterDashboardTitle
          loading={this.state.loading}
          thisPeriod={this.state.selectedSession.session_plan_id}
          setThisPeriod={this.setThisPeriod}
          application_sessions={this.props.sessionPlan.allSessionPlans}
          chooseDistrict={this.state.chooseDistrict}
          setChooseDistrict={this.setChooseDistrict}
          selectedDistrict={this.state.selectedDistrict}
          setSelectedDistrict={this.setSelectedDistrict}
          reportMainData={this.state.reportMainData}
          chooseExam={this.state.chooseExam}
          setChooseExam={this.setChooseExam}
          selectedExam={this.state.selectedExam}
          setSelectedExam={this.setSelectedExam}
          examsList={this.props.examsList}
          courses={this.props.courses}
        />

        {this.state.reportMainData && (
          <section className="my-2">
            <div className="text-2xl font-semibold text-gray-600">
              Room occupation status
            </div>
            <ExamDashboardLabels
              dataLabels={[
                {
                  title: "Total room",
                  icon: FaLaptopHouse,
                  iconColor: "blue",
                  count: filterRoom(
                    roomWithOccupation(
                      this.state.reportMainData,
                      this.state.invigilatorData
                    ),
                    this.state.selectedDistrict
                  ).all.length,
                },
                {
                  title: "Full room",
                  icon: FaLaptopHouse,
                  iconColor: "green",
                  count: filterRoom(
                    roomWithOccupation(
                      this.state.reportMainData,
                      this.state.invigilatorData
                    ),
                    this.state.selectedDistrict
                  ).full.length,
                },
                {
                  title: "Available room",
                  icon: FaLaptopHouse,
                  iconColor: "gray",
                  count: filterRoom(
                    roomWithOccupation(
                      this.state.reportMainData,
                      this.state.invigilatorData
                    ),
                    this.state.selectedDistrict
                  ).remain.length,
                },
                {
                  title: "Room with invigilator",
                  icon: FaLaptopHouse,
                  iconColor: "gray",
                  count: filterRoom(
                    roomWithOccupation(
                      this.state.reportMainData,
                      this.state.invigilatorData
                    ),
                    this.state.selectedDistrict
                  ).invigilator.length,
                },
              ]}
              labelsOnRow={5}
            >
              <div className="flex justify-center">
                <PieChart
                  options={{
                    labels: ["Prepared", "Remaining"],
                    legend: {
                      show: false,
                      position: "bottom",
                      horizontalAlign: "center",
                      fontSize: "16px",
                      itemMargin: {
                        horizontal: 3,
                        vertical: 0,
                      },
                    },
                    plotOptions: {
                      pie: {
                        donut: {
                          labels: {
                            show: true,
                            name: {
                              show: true,
                              fontSize: "16px",
                              //   fontFamily: "Rubik",
                              color: "#2564eb",
                              offsetY: -10,
                            },
                            value: {
                              show: true,
                              fontSize: "20px",
                              fontFamily: "Helvetica, Arial, sans-serif",
                              color: "#2564eb",
                              offsetY: 10,
                              formatter: function (val) {
                                return val;
                              },
                            },
                            total: {
                              show: true,
                              label: "Total",
                              color: "#2564eb",
                              formatter: function (w) {
                                return w.globals.seriesTotals.reduce(
                                  (a: number, b: number) => {
                                    return a + b;
                                  },
                                  0
                                );
                              },
                            },
                          },
                        },
                      },
                    },
                    colors: ["#469769", "#4b5563"],
                  }}
                  series={[
                    filterRoom(
                      roomWithOccupation(
                        this.state.reportMainData,
                        this.state.invigilatorData
                      ),
                      this.state.selectedDistrict
                    ).full.length,
                    filterRoom(
                      roomWithOccupation(
                        this.state.reportMainData,
                        this.state.invigilatorData
                      ),
                      this.state.selectedDistrict
                    ).remain.length,
                  ]}
                  width={210}
                />
              </div>
            </ExamDashboardLabels>
          </section>
        )}

        <section className="my-3 max-w-full bg-white">
          <div className="flex items-center justify-between px-3">
            <span className="text-gray-600 font-semibold text-xl py-4 px-0">
              Room availability{" "}
              {this.state.selectedDistrict === "all"
                ? "on country level"
                : "in " +
                  this.state.selectedDistrict.district_name +
                  " district"}
            </span>
          </div>
          <div className="mb-4 pb-2 px-3">
            <RoomChart
              allData={this.roomDataFilter(
                roomWithOccupation(
                  this.state.reportMainData,
                  this.state.invigilatorData
                ),
                this.state.selectedExam
              )}
              selectedExam={this.state.selectedExam}
            />
          </div>
        </section>
      </section>
    );
  }
}

const mapStateToProps = ({
  auth,
  sessionPlan,
  course,
  prepareExam,
}: StoreState): {
  auth: Auth;
  sessionPlan: SessionPlanReducerInterface;
  courses: course[] | null;
  examsList: ExamItemResponse[] | null;
} => {
  return {
    auth: auth,
    sessionPlan,
    courses: course.courses,
    examsList: prepareExam.examsList,
  };
};

export const CenterDashboard = connect(mapStateToProps, {
  GetAllSessionPlans,
  loadCourses,
  loadExamsList,
  PushPopupAlert,
})(_CenterDashboard);
