import React, { Fragment, useState } from "react";
import { AiFillDashboard } from "react-icons/ai";
import { CgFileDocument } from "react-icons/cg";
import { FiEdit } from "react-icons/fi";
import Loading from "../../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { course, SessionPlanInterface } from "../../../interfaces";
import {
  centerReportInterface,
  getDistrictInRoom,
  roomDistrictInterface,
} from "../../../interfaces/center_report";
import { ExamItemResponse } from "../../../interfaces/prepare_exam";
import DistrictList from "../../Admin/CenterReport/DistrictList";
import {
  examPreparationStateInterface,
  FC_reportExamList,
  searchExamList,
} from "../../Admin/ExamReport/ExamList";

type CenterReportTitleProps = {
  // FOR SETTINGS ==============================
  loading: boolean;

  //   FOR APPLICATION SESSION ============================
  thisPeriod: string;
  setThisPeriod: (
    allSession: SessionPlanInterface[],
    sessionId: string
  ) => void;
  application_sessions: SessionPlanInterface[];
  chooseDistrict: boolean;
  setChooseDistrict: (status: boolean) => void;
  selectedDistrict: roomDistrictInterface | "all";
  setSelectedDistrict: (data: roomDistrictInterface | "all") => void;
  chooseExam: boolean;
  setChooseExam: (status: boolean) => void;
  selectedExam: examPreparationStateInterface | "all";
  setSelectedExam: (data: examPreparationStateInterface | "all") => void;
  reportMainData: centerReportInterface[];
  examsList: ExamItemResponse[] | null;
  courses: course[] | null;
};

const CenterDashboardTitle: React.FC<CenterReportTitleProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");

  return (
    <Fragment>
      <div className="mt-3 rounded flex flex-col md:flex-row md:justify-between items-start">
        <div className="flex flex-row items-center gap-3 mb-2">
          <AiFillDashboard className="text-7xl text-blue-600 hidden md:block" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-3xl font-bold">
              Center Dashboard
            </span>
          </div>
        </div>

        {/* FILTER SECTION PERIOD && EDUCATION */}
        <section className="w-full flex flex-col items-end md:w-auto my-2 md:my-0">
          <div>
            <select
              className={`bg-white text-gray-600 border border-gray-400 rounded p-2 focus:outline-none ${
                props.loading && "cursor-wait"
              }`}
              value={props.thisPeriod}
              disabled={props.loading}
              onChange={(e) => {
                props.setThisPeriod(props.application_sessions, e.target.value);
              }}
            >
              {props.application_sessions.map((session, item) => (
                <option
                  key={session.session_plan_id + item}
                  value={session.session_plan_id}
                >
                  {session.session_plan_name}
                </option>
              ))}
            </select>
          </div>
          {/* DISTRICT sections */}
          <div className="mt-1 w-full">
            <div
              onClick={() => props.setChooseDistrict(true)}
              className="font-bold bg-gray-50 border border-gray-400 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-gray-600 w-full flex flex-row items-center animate__animated animate__zoomIn"
            >
              {props.selectedDistrict !== "all"
                ? `${props.selectedDistrict.district_name}`
                : "All district"}
              <FiEdit className="text-lg text-gray-600" />
            </div>
          </div>
          {/* end DISTRICT sections */}

          {/* DISTRICT sections */}
          <div className="mt-1 w-full">
            <div
              onClick={() => props.setChooseExam(true)}
              className="font-bold bg-gray-50 border border-gray-400 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-gray-600 w-full flex flex-row items-center animate__animated animate__zoomIn"
            >
              {props.selectedExam !== "all"
                ? `${props.selectedExam.course_name}`
                : "All exam"}
              <FiEdit className="text-lg text-gray-600" />
            </div>
          </div>
          {/* end DISTRICT sections */}
        </section>
        {/* END FILTER SECTION PERIOD && EDUCATION */}
      </div>

      {props.chooseDistrict && (
        <Modal
          title={
            <div className="flex flex-row gap-4 items-center">
              <CgFileDocument className="text-3xl text-blue-600" />
              <div className="flex flex-col">
                <span className="font-bold text-xl text-blue-600">
                  Choose district
                </span>
              </div>
            </div>
          }
          backDrop={true}
          theme={Themes.default}
          close={() => props.setChooseDistrict(false)}
          backDropClose={true}
          widthSizeClass={ModalSize.small}
          displayClose={true}
          padding={{ title: true }}
        >
          <DistrictList
            setChooseDistrict={props.setChooseDistrict}
            districtList={getDistrictInRoom(props.reportMainData)}
            setSelectedDistrict={props.setSelectedDistrict}
            loadindRequest={props.loading}
            isModal={true}
            selectedDistrict={props.selectedDistrict}
          />
        </Modal>
      )}

      {props.chooseExam && (
        <Modal
          title={
            <div className="flex flex-row gap-4 items-center">
              <CgFileDocument className="text-5xl text-blue-600" />
              <div className="flex flex-col">
                <span className="font-bold text-xl text-blue-600">
                  Choose exam
                </span>
              </div>
            </div>
          }
          backDrop={true}
          theme={Themes.default}
          close={() => props.setChooseExam(false)}
          backDropClose={true}
          widthSizeClass={ModalSize.extraLarge}
          displayClose={true}
          padding={{ title: true }}
        >
          {props.courses === null || props.examsList === null ? (
            <Loading />
          ) : (
            <div className="px-2 pb-2">
              <div className="w-full py-2">
                <input
                  onChange={(e) => setSearchInput(e.target.value)}
                  type="text"
                  value={searchInput}
                  placeholder="Search..."
                  className="w-full bg-white py-2 px-3 rounded border border-gray-400"
                  disabled={
                    FC_reportExamList(props.courses, props.examsList).length <=
                    0
                      ? true
                      : false
                  }
                />
              </div>
              <div
                onClick={() => {
                  props.setSelectedExam("all");
                  props.setChooseExam(false);
                }}
                className={`flex items-center border rounded px-2 py-2 cursor-pointer group hover:bg-blue-100 hover:border-blue-500 mb-3 justify-between ${
                  props.selectedExam === "all"
                    ? "bg-blue-50 border-blue-400"
                    : "bg-white border-gray-200"
                }`}
              >
                <div>
                  <div className="text-sm font-medium italic">
                    code: <span className="text-blue-500">-</span>
                  </div>
                  <div className="font-bold text-lg">All exams</div>
                </div>
                <div className="btn bg-blue-400 group-hover:bg-blue-600 text-white mr-2">
                  Choose
                </div>
              </div>
              {searchExamList(
                FC_reportExamList(props.courses, props.examsList),
                searchInput
              ).map((item, i) => (
                <div
                  onClick={() => {
                    props.setSelectedExam(item);
                    props.setChooseExam(false);
                  }}
                  key={i + 1}
                  className={`flex items-center border rounded px-2 py-2 cursor-pointer group hover:bg-blue-100 hover:border-blue-500 mb-3 justify-between ${
                    props.selectedExam !== "all" &&
                    props.selectedExam.course_id === item.course_id
                      ? "bg-blue-50 border-blue-400"
                      : "bg-white border-gray-200"
                  }`}
                >
                  <div>
                    <div className="text-sm font-medium italic">
                      code:{" "}
                      <span className="text-blue-500">{item.course_code}</span>
                    </div>
                    <div className="font-bold text-lg">{item.course_name}</div>
                  </div>
                  <div className="btn bg-blue-400 group-hover:bg-blue-600 text-white mr-2">
                    Choose
                  </div>
                </div>
              ))}
            </div>
          )}
        </Modal>
      )}
    </Fragment>
  );
};

export default CenterDashboardTitle;
