  distinctDistricts = (): string[] => {
    let district_ids: string[] = [];
    for (const key in this.props.allData) {
      if (!district_ids.includes(this.props.allData[key].district_id)) {
        district_ids.push(this.props.allData[key].district_id);
      }
    }
    return district_ids;
  };

  generateChartData = (): {
    series: {
      name: string;
      data: number[];
    }[];
    categories: (string | string[])[];
    labels: string[];
  } => {
    let categories: (string | string[])[] = [],
      labels: string[] = [],
      occupied: number[] = [],
      remaining: number[] = [],
      district_ids: string[] = [];
    let thisOccupation: number = 0,
      thisRemaining: number = 0,
      thisDefaultOccupation: number = 0,
      thisDefaultRemaining: number = 0,
      districtName: string = "";
    this.distinctDistricts().forEach((district) => {
      const thisDistrict = this.props.allData.filter(
        (item) => item.district_id === district
      );

      for (const key in thisDistrict) {
        if (thisDistrict[key].room_number !== 0) {
          districtName = thisDistrict[key].district_name;
          const selectedExam = this.props.selectedExam;
          if (selectedExam !== "all") {
            thisDefaultOccupation = thisDistrict[key].course_in_room.find(
              (itemCourse) => itemCourse.course_id === selectedExam.course_id
            )
              ? thisDistrict[key].course_in_room.find(
                  (itemCourse) =>
                    itemCourse.course_id === selectedExam.course_id
                )!.occupied
              : 0;
            thisDefaultRemaining =
              thisDistrict[key].capacity -
              (thisDistrict[key].course_in_room.find(
                (itemCourse) => itemCourse.course_id === selectedExam.course_id
              )
                ? thisDistrict[key].course_in_room.find(
                    (itemCourse) =>
                      itemCourse.course_id === selectedExam.course_id
                  )!.occupied
                : 0);
          } else {
            thisDefaultOccupation = thisDistrict[key].occupied;
            thisDefaultRemaining =
              thisDistrict[key].capacity - thisDistrict[key].occupied;
          }
          thisOccupation += thisDefaultOccupation;
          thisRemaining += thisDefaultRemaining;
        }
      }
      if (!district_ids.includes(district)) {
        district_ids.push(district);
        occupied.push(thisOccupation);
        remaining.push(thisRemaining);
        categories.push([districtName]);
        labels.push(districtName);
      }
      districtName = "";
      thisRemaining = 0;
      thisOccupation = 0;
    });

    return {
      series: [
        {
          name: "Occupied room",
          data: occupied,
        },
        {
          name: "Remaining room",
          data: remaining,
        },
      ],
      categories,
      labels,
    };
  };

  render() {
    return (
      <Fragment>
        <div className="w-full">
          <VBarChart
            labels={this.generateChartData().labels}
            categoriesLabel={this.generateChartData().categories}
            dataLabels={true}
            yaxisText=" sit(s)"
            tooltipText=" sit(s)"
            series={this.generateChartData().series}
            colors={["#059669", "#374151"]}
            defaultBar={{
              borderRadius: 4,
              horizontal: true,
              columnWidth: "95%",
              endingShape: "rounded",
            }}
            // chartHeight={
            //   this.generateChartData().series.length <= 10 ? 550 : 1000
            // }
            stacked={true}
          />
        </div>
      </Fragment>
    );
  }
